<template>
  <div id="app" class="backgroundme">
    <!-- <div class="small-container">  -->
    <dod-form @ask:question="askQuestion" class="CenterMe" />
    
    <!-- </div> -->
    <!-- <div class="answer"> -->
    
    <p class="CenterP">{{ current_answer }}</p>
    
    <!-- </div> -->
    <!-- <div class="history"> -->
    <dod-table :dods="dods" class="CenterMe" />
    <!-- </div> -->
  </div>
</template>

<script>
import DodForm from '@/components/DodForm.vue'
import DodTable from '@/components/DodTable.vue'
import axios from 'axios'

export default {
  name: 'app',
  components: {
    DodForm,
    DodTable
  },
  data() {
    return {
      dods: [
      ],
      current_answer: '',
      current_id: 1,
    }
  },
  methods: {
  async askQuestion(dod) {
      let dodurl = 'https://dod-api-server.azurewebsites.net/dod/' + dod.question;
      await axios.get(dodurl).then(response => (this.current_answer = response.data.data))
      const id = this.current_id;
      const newDod = { ...dod, id };
      newDod.answer = this.current_answer;
      this.dods = [newDod, ...this.dods]
      dod.question = ''
      this.current_id ++
    }
  }
}
</script>

