<template>
  <div id="dod-form">
    <form @submit.prevent="handleSubmit">
        <div class="Row">
    <div class="Column">
        <input v-model="dod.question" type="text" />
    </div>
      <div class="Column">
        <button>Ask Question</button>
      </div>
        </div>
    </form>
  </div>
</template>

<script>
  export default {
    name: 'dod-form',
    data() {
      return {
        dod: {
          question: '',
          answer: '',
        },
      }
    },
    methods: {
      handleSubmit() {
        this.$emit('ask:question', this.dod)
      },
    },
  }
</script>

<style scoped>
  form {
    margin-bottom: 2rem;
  }
</style>

<style>
.form-inline { 
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

/* Add some margins for each label */
.form-inline label {
  margin: 5px 10px 5px 0;
}

/* Style the input fields */
.form-inline input {
  vertical-align: middle;
  margin: 5px 10px 5px 0;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #ddd;
}

/* Style the submit button */
.form-inline button {
  background-color: dodgerblue;
  border: 1px solid #ddd;
  color: white;
}
.Row
    {
        display: table;
        width: 100%; /*Optional*/
        table-layout: fixed; /*Optional*/
        border-spacing: 10px; /*Optional*/
    }
